import React, { Component } from "react";
// import { compose } from "redux";
// import { connect } from "react-redux";
// import { Link } from "react-router-dom";
import moment from "moment";

// import component
import Button from "../../../../components/uielements/button";
import
// CardWrapper, 
{
  // Box, 
  customCollapseStyle
} from "../../../App/app.style";
import IntlMessages from "../../../../components/utility/intlMessages";
// import LayoutWrapper from "../../../../components/utility/layoutWrapper";
// import NumberFormat from "../../../App/NumberFormat";
// import PageHeader from "../../../../components/utility/pageHeader";
// import Scrollbars from "../../../../components/utility/customScrollBar";
// import TableWrapper from "../../../Tables/antTables/antTable.style";
// import notification from "../../../../components/notification";
import {
  Col,
  Collapse,
  // Empty,
  DatePicker,
  // Divider,
  // Input,
  Row,
  Select,
  // Tooltip,
  TreeSelect
} from "antd";

// import action
// import actions from "../../../../redux/report/accountHistoryReport/actions";
// import getLanguage from "../../../App/MyLanguange";
// import PrintWrapper from "../../../App/PrintWrapper";
// import ReactToPrint from "react-to-print";
// import ButtonPrint from "../../../App/ButtonPrint";
// import ExcelWrapper from "../../../App/ExcelWrapper";
// import PDFWrapper from "../../../App/PDFWrapper";

const Option = Select.Option;
const Panel = Collapse.Panel;
// const Search = Input.Search;
const RangePicker = DatePicker.RangePicker;
// const { getAccountHistoryReports } = actions;

const loop = array =>
  array.map(data => {
    let perData;
    if (data.is_active === 0) {
      perData = {
        title: data.account_code + ". " + data.account_name,
        value: data.id_account,
        key: data.id_account,
        disabled: true
      };
    } else {
      perData = {
        title: data.account_code + ". " + data.account_name,
        value: data.id_account,
        key: data.id_account,
        children: data.children ? loop(data.children) : null,
        disabled: data.children.length > 0 ? true : false
      };
    }
    return perData;
  });

// const redirectTo = "/dashboard/report-menus";

class Filter extends Component {
  state = {
    filter: {
      start_date: moment()
        .startOf("month")
        .format("YYYY-MM-DD HH:mm:ss"),
      end_date: moment().format("YYYY-MM-DD HH:mm:ss"),
      id_account: null,
      id_location: null,
      sort_by: "transaction_date",
      sort_type: "asc"
    },
    search: null
  };

  render() {
    const {
      accounts,
      end_date,
      id_account,
      id_location,
      loadingForeign,
      locations,
      sort_by,
      sort_type,
      start_date
    } = this.props;

    //const { current_page, page_size, total } = this.props.accountHistory;

    return (
      <div className="isoFilter">
        <Collapse style={customCollapseStyle} defaultActiveKey={["1"]}>
          <Panel header={<IntlMessages id="filter.header" />} key="1">
            <Row
              justify="start"
              type="flex"
              style={{ marginBottom: "10px" }}
            >
              <Col span={12}>
                <label>
                  <IntlMessages id="sidebar.account" />
                </label>
                <br />
                <TreeSelect
                  // allowClear={true}
                  dropdownStyle={{ maxHeight: 300, overflow: "auto" }}
                  loading={loadingForeign}
                  onSelect={this.props.handleSelectAccount}
                  placeholder={<IntlMessages id="accountName.select" />}
                  style={{ width: "95%" }}
                  showSearch={true}
                  treeNodeFilterProp="title"
                  treeData={
                    accounts && accounts.length > 0 ? loop(accounts) : null
                  }
                  treeDefaultExpandAll
                  value={
                    id_account
                      ? id_account
                      : null
                  }
                />
              </Col>
              <Col span={12}>
                <label>
                  <IntlMessages id="locations.pageHeader" />
                </label>
                <br />
                <Select
                  showSearch
                  onChange={this.props.handleFilterChange.bind(
                    this,
                    "id_location"
                  )}
                  value={
                    id_location
                      ? id_location
                      : []
                  }
                  style={{ width: "100%" }}
                  placeholder={<IntlMessages id="select.location" />}
                  optionFilterProp="children"
                  filterOption={true}
                  loading={loadingForeign}
                  allowClear={true}
                //showArrow={false}
                >
                  {locations.map((data, key) => (
                    <Option key={key} value={data.id_location}>
                      {`${data.location_code} (${data.location_name})`}
                    </Option>
                  ))}
                </Select>
              </Col>
            </Row>
            <Row
              justify="start"
              type="flex"
              style={{ marginBottom: "24px" }}
            >
              <Col span={12}>
                <label>
                  <IntlMessages id="salesOrders.filter.date" />
                </label>
                <br />
                <RangePicker
                  format="DD MMMM YYYY HH:mm:ss"
                  showTime
                  //allowClear={true}
                  style={{ width: "95%" }}
                  value={
                    start_date &&
                      end_date
                      ? [
                        moment(start_date),
                        moment(end_date)
                      ]
                      : []
                  }
                  onChange={this.props.handleFilterChange.bind(this, "date")}
                />
              </Col>
              <Col span={12}>
                <Row justify="start" gutter={1} type="flex">
                  <Col span={8}>
                    <label>
                      <IntlMessages id="sortBy" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.props.handleFilterChange.bind(
                        this,
                        "sort_by"
                      )}
                      value={
                        sort_by
                          ? sort_by
                          : "transaction_date"
                      }
                      style={{ width: "95%" }}
                      // placeholder={
                      //   <IntlMessages id="stockValuationReports.sortBy.select" />
                      // }
                      optionFilterProp="children"
                      filterOption={true}
                    //allowClear={true}
                    //showArrow={false}
                    >
                      <Option key={1} value="account_code">
                        <IntlMessages id="accountCode" />
                      </Option>
                      <Option key={2} value="account_name">
                        <IntlMessages id="accountName" />
                      </Option>
                      {/* <Option key={3} value="transaction_type">
                        <IntlMessages id="transactionType" />
                      </Option> */}
                      <Option key={4} value="transaction_date">
                        <IntlMessages id="transactionDate" />
                      </Option>
                      <Option key={5} value="source_transaction_no">
                        <IntlMessages id="transactionNo" />
                      </Option>
                    </Select>
                  </Col>
                  <Col span={8}>
                    <label>
                      <IntlMessages id="sortType" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.props.handleFilterChange.bind(
                        this,
                        "sort_type"
                      )}
                      value={
                        sort_type
                          ? sort_type
                          : "asc"
                      }
                      style={{ width: "95%" }}
                      // placeholder={
                      //   <IntlMessages id="stockValuationReports.sortBy.select" />
                      // }
                      optionFilterProp="children"
                      filterOption={true}
                    //allowClear={true}
                    //showArrow={false}
                    >
                      <Option key={1} value="asc">
                        <IntlMessages id="ascending" />
                      </Option>
                      <Option key={2} value="desc">
                        <IntlMessages id="descending" />
                      </Option>
                    </Select>
                  </Col>
                  <Col
                    span={8}
                    style={{
                      marginTop: "24px"
                    }}
                  >
                    <Button
                      size="small"
                      type="primary"
                      style={{ marginRight: "10px" }}
                      onClick={this.props.handleFilter}
                    >
                      <IntlMessages id="filter.submit" />
                    </Button>
                    <Button onClick={this.props.handleResetFilter} size="small">
                      <IntlMessages id="filter.reset" />
                    </Button>
                  </Col>
                </Row>
              </Col>
            </Row>
          </Panel>
        </Collapse>
      </div>
    );
  }
}

export default Filter;
