import React, { Component } from "react";
import { compose } from "redux";
import { connect } from "react-redux";
// import { Link } from "react-router-dom";
import moment from "moment";

// import component
import Button from "../../../../components/uielements/button";
import CardWrapper, { Box } from "../../../App/app.style";
import IntlMessages from "../../../../components/utility/intlMessages";
import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import NumberFormat from "../../../App/NumberFormat";
import PageHeader from "../../../../components/utility/pageHeader";
import Scrollbars from "../../../../components/utility/customScrollBar";
import TableWrapper from "../../../Tables/antTables/antTable.style";
import notification from "../../../../components/notification";
import {
  // Col,
  // Collapse,
  Empty,
  // DatePicker,
  Divider,
  Input,
  // Row,
  // Select,
  // Tooltip,
  // TreeSelect,
  Spin
} from "antd";

// import action
import actions from "../../../../redux/report/accountHistoryReport/actions";
import getLanguage from "../../../App/MyLanguange";
import Filter from "./filter";
import PrintWrapper from "../../../App/PrintWrapper";
import ReactToPrint from "react-to-print";
import ButtonPrint from "../../../App/ButtonPrint";
import ExcelWrapper from "../../../App/ExcelWrapper";
import PDFWrapper from "../../../App/PDFWrapper";
import { siteConfig } from "../../../../settings";
import { getFileName } from "../../../../helpers/helper";

// const Option = Select.Option;
// const Panel = Collapse.Panel;
const Search = Input.Search;
// const RangePicker = DatePicker.RangePicker;
// const { getAccountHistoryReports } = actions;

const loop = array =>
  array.map(data => {
    let perData;
    if (data.is_active === 0) {
      perData = {
        title: data.account_code + ". " + data.account_name,
        value: data.id_account,
        key: data.id_account,
        disabled: true
      };
    } else {
      perData = {
        title: data.account_code + ". " + data.account_name,
        value: data.id_account,
        key: data.id_account,
        children: data.children ? loop(data.children) : null,
        disabled: data.children.length > 0 ? true : false
      };
    }
    return perData;
  });

const redirectTo = "/dashboard/report-menus";

class AccountHistoryReport extends Component {
  state = {
    filter: {
      start_date: moment()
        .startOf("month")
        .format("YYYY-MM-DD HH:mm:ss"),
      end_date: moment().format("YYYY-MM-DD HH:mm:ss"),
      id_account: null,
      id_location: null,
      sort_by: "transaction_date",
      sort_type: "asc"
    },
    search: null
  };

  componentDidMount() {
    const { getAccountHistoryReports } = this.props;
    getAccountHistoryReports();
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.accountHistoryReports.error) {
      notification("error", this.props.accountHistoryReports.error);
      this.props.clearError();
    }
    // Display success notif
    if (this.props.accountHistoryReports.success) {
      notification("success", this.props.accountHistoryReports.success);
      this.props.clearSuccess();
    }
  }

  filterValue = () => {
    const { filter } = this.state;
    let string = "";

    if (filter.id_location) {
      string
        ? (string += "&id_location=" + filter.id_location)
        : (string += "id_location=" + filter.id_location);
    }
    if (filter.id_account) {
      string
        ? (string += "&id_account=" + filter.id_account)
        : (string += "id_account=" + filter.id_account);
    }
    if (filter.start_date && filter.end_date) {
      string
        ? (string +=
            "&start_date=" + filter.start_date + "&end_date=" + filter.end_date)
        : (string +=
            "start_date=" + filter.start_date + "&end_date=" + filter.end_date);
    }

    string
      ? (string +=
          "&sort_by=" + filter.sort_by + "&sort_type=" + filter.sort_type)
      : (string +=
          "sort_by=" + filter.sort_by + "&sort_type=" + filter.sort_type);

    return string;
  };

  // Handle filter
  handleFilter = () => {
    const { getAccountHistoryReports } = this.props;
    let filter = this.filterValue();
    this.setState({ search: null });
    getAccountHistoryReports(1, null, filter, this.state.filter);
  };

  //   Handle filter change
  handleFilterChange = (name, value) => {
    if (name === "date" && value[0] && value[1]) {
      const start = value[0].format("YYYY-MM-DD HH:mm:ss");
      const end = value[1].format("YYYY-MM-DD HH:mm:ss");
      // console.log(start, end);
      this.setState({
        filter: {
          ...this.state.filter,
          start_date: start,
          end_date: end
        }
      });
    } else if (name === "date" && !value[0] && !value[1]) {
      this.setState({
        filter: {
          ...this.state.filter,
          start_date: null,
          end_date: null
        }
      });
    } else {
      this.setState({
        filter: {
          ...this.state.filter,
          [name]: value
        }
      });
    }
  };

  handleSelectAccount = value => {
    const { accounts } = this.props.accountHistoryReports;
    let obj = null;
    const filter = array => {
      array.map(data => {
        if (data.children) {
          if (data.id_account === value) {
            obj = data;
          } else {
            filter(data.children);
          }
        } else {
          if (data.id_account === value) {
            obj = data;
          } else {
            obj = null;
          }
        }
      });
    };
    filter(accounts);
    obj = Object.assign({}, obj, { children: null });
    this.setState({
      filter: {
        ...this.state.filter,
        id_account: obj.id_account
      }
    });
  };

  // Handle action di table
  handleRecord = (action, id) => {
    // console.log(action, data);
    if (action === "activate") {
      // data.is_active = 1;
      this.props.activateSupplierPriceList(id);
    } else {
      // data.is_active = 0;
      this.props.deactivateSupplierPriceList(id);
    }
  };

  // Handle Reset Filter
  handleResetFilter = () => {
    const { getAccountHistoryReports } = this.props;
    this.setState({
      filter: {
        start_date: moment()
          .startOf("month")
          .format("YYYY-MM-DD HH:mm:ss"),
        end_date: moment().format("YYYY-MM-DD HH:mm:ss"),
        id_account: null,
        id_location: null,
        sort_by: "transaction_date",
        sort_type: "asc"
      }
    });
    this.setState({ search: null });
    getAccountHistoryReports();
  };

  // Handle Reset search
  handleResetSearch = () => {
    const { getAccountHistoryReports } = this.props;
    this.setState({
      filter: {
        start_date: moment()
          .startOf("month")
          .format("YYYY-MM-DD HH:mm:ss"),
        end_date: moment().format("YYYY-MM-DD HH:mm:ss"),
        id_account: null,
        id_location: null,
        sort_by: "transaction_date",
        sort_type: "asc"
      }
    });
    this.setState({ search: null });
    getAccountHistoryReports();
  };

  handleSearch = value => {
    const { getAccountHistoryReports } = this.props;
    let filter = this.filterValue();
    getAccountHistoryReports(1, value, filter, this.state.filter);
  };

  //handle Search Value
  handleSearchValue = e => {
    const value = e.target.value;
    this.setState({ search: value });
  };

  renderTransTypeString = data => {
    let transaction_type_string;

    switch (data) {
      case 1:
        transaction_type_string = getLanguage({ id: "openingBalance" });
        break;
      case 2:
        transaction_type_string = getLanguage({ id: "sidebar.issueReceipt" });
        break;
      case 3:
        transaction_type_string = getLanguage({ id: "sidebar.issueReceipt" });
        break;
      case 4:
        transaction_type_string = getLanguage({
          id: "sidebar.purchaseReceipts"
        });
        break;
      case 5:
        transaction_type_string = getLanguage({
          id: "sidebar.purchaseInvoices"
        });
        break;
      case 6:
        transaction_type_string = getLanguage({
          id: "sidebar.purchaseReturns"
        });
        break;
      case 7:
        transaction_type_string = getLanguage({ id: "sidebar.itemTransfer" });
        break;
      case 8:
        transaction_type_string = getLanguage({ id: "sidebar.deliveryOrder" });
        break;
      case 9:
        transaction_type_string = getLanguage({ id: "sidebar.salesInvoice" });
        break;
      case 11:
        transaction_type_string = getLanguage({ id: "sidebar.salesReturn" });
        break;
      case 12:
        transaction_type_string = getLanguage({ id: "sidebar.fixedAsset" });
        break;
      case 13:
        transaction_type_string = getLanguage({ id: "journalVoucher" });
        break;
      case 14:
        transaction_type_string = getLanguage({
          id: "accountReceivablePayment"
        });
        break;
      case 15:
        transaction_type_string = getLanguage({ id: "accountPayablePayment" });
        break;
      case 16:
        transaction_type_string = getLanguage({ id: "creditMemo" });
        break;
      case 17:
        transaction_type_string = getLanguage({ id: "debitMemo" });
        break;
      case 18:
        transaction_type_string = getLanguage({ id: "sidebar.cashManagement" });
        break;
      case 19:
        transaction_type_string = getLanguage({ id: "sidebar.jobCosting" });
        break;
      case 20:
        transaction_type_string = getLanguage({
          id: "sidebar.jobCostRollover"
        });
        break;
      case 21:
        transaction_type_string = getLanguage({ id: "supplierDeposit" });
        break;
      case 22:
        transaction_type_string = getLanguage({ id: "customerDeposit" });
        break;
      case 23:
        transaction_type_string = getLanguage({
          id: "sidebar.fixedAssetRevaluation"
        });
        break;
      case 24:
        transaction_type_string = getLanguage({ id: "Raptor Sales Invoice" });
        break;
      case 25:
        transaction_type_string = getLanguage({ id: "Raptor Sales Return" });
        break;
      case 26:
        transaction_type_string = getLanguage({ id: "POS Sales Invoice" });
        break;
      case 27:
        transaction_type_string = getLanguage({ id: "POS Sales Return" });
        break;
      default:
        transaction_type_string = "-";
    }
    return transaction_type_string;
  };

  render() {
    const { accountHistoryReports } = this.props;
    const {
      accounts,
      // account_types,
      loadingForeign,
      loading,
      locations,
      account_history_reports,
      filter,
      filter_select
    } = accountHistoryReports;

    // Definisi kolom2 serta field2 API yang dipakai tabel
    const excelMiniColumns = [
      {
        title: getLanguage({ id: "transactionDate" }),
        dataIndex: "transaction_date",
        rowKey: "transaction_date",
        width: "8%",
        spanCheck: "string",
        spanCheckValue: "Total",
        spanValue: 5
      },
      {
        title: getLanguage({ id: "transactionType" }),
        dataIndex: "transaction_type_string",
        rowKey: "transaction_type_string",
        width: "8%",
        spanCheck: "string",
        spanCheckValue: "Total",
        spanValue: 0
      },
      {
        title: getLanguage({ id: "transactionNo" }),
        dataIndex: "transaction_no",
        rowKey: "transaction_no",
        width: "8%",
        spanCheck: "string",
        spanCheckValue: "Total",
        spanValue: 0
      },
      {
        title: getLanguage({ id: "accountCode" }),
        dataIndex: "account_code",
        rowKey: "account_code",
        width: "8%",
        spanCheck: "string",
        spanCheckValue: "Total",
        spanValue: 0
      },
      {
        title: getLanguage({ id: "accountName" }),
        dataIndex: "account_name",
        rowKey: "account_name",
        width: "12%",
        spanCheck: "string",
        spanCheckValue: "Total",
        spanValue: 0
      },
      {
        align: "right",
        title: getLanguage({ id: "debit" }),
        dataIndex: "debit",
        rowKey: "debit",
        parse: true,
        width: "10%"
      },
      {
        align: "right",
        title: getLanguage({ id: "credit" }),
        dataIndex: "credit",
        rowKey: "credit",
        parse: true,
        width: "10%"
      }
    ];

    const columns = [
      {
        title: getLanguage({ id: "accountCode" }),
        dataIndex: "account_code",
        rowKey: "account_code",
        width: "8%"
      },
      {
        title: getLanguage({ id: "accountName" }),
        dataIndex: "account_name",
        rowKey: "account_name",
        width: "12%"
      },
      {
        title: getLanguage({ id: "date" }),
        dataIndex: "transaction_date",
        date: true,
        rowKey: "transaction_date",
        width: "12%",
        render: text => {
          return <span>{moment(text).format("DD MMMM YYYY HH:mm:ss")}</span>;
        }
      },
      {
        title: getLanguage({ id: "transactionType" }),
        dataIndex: "transaction_type_string",
        rowKey: "transaction_type_string",
        width: "12%",
        render: text => {
          return <span>{text}</span>;
        }
      },
      {
        title: getLanguage({ id: "transactionNo" }),
        dataIndex: "transaction_no",
        rowKey: "transaction_no",
        width: "14%"
      },
      {
        title: getLanguage({ id: "createdBy" }),
        dataIndex: "created_by",
        rowKey: "created_by",
        width: "8%"
      },
      {
        title: getLanguage({ id: "currency" }),
        dataIndex: "currency.currency_code",
        rowKey: "currency.currency_code",
        width: "7%"
      },
      {
        align: "right",
        title: getLanguage({ id: "debit" }),
        dataIndex: "debit",
        rowKey: "debit",
        parse: true,
        width: "10%",
        render: (text, row) => {
          if (row.debit_credit === 1) {
            return (
              <span>
                <NumberFormat value={parseFloat(row.amount)} />
              </span>
            );
          } else {
            return "-";
          }
        }
      },
      {
        align: "right",
        title: getLanguage({ id: "credit" }),
        dataIndex: "credit",
        rowKey: "credit",
        parse: true,
        width: "10%",
        render: (text, row) => {
          if (row.debit_credit === 2) {
            return (
              <span>
                <NumberFormat value={parseFloat(row.amount)} />
              </span>
            );
          } else {
            return "-";
          }
        }
      }
    ];

    // --- init variable
    let dataSource = [];
    let excelDataSource = [];
    if (account_history_reports && account_history_reports.length > 0) {
      account_history_reports.map((data, index) => {
        var transaction_type_string = this.renderTransTypeString(
          data.transaction_type
        );
        var debit = "";
        var credit = "";
        if (data.debit_credit === 1) {
          debit = data.amount;
        } else {
          credit = data.amount;
        }
        excelDataSource.push({
          ...data,
          credit: credit,
          debit: debit,
          transaction_type_string: transaction_type_string
        });
        return dataSource.push({
          ...data,
          key: index,
          transaction_type_string: transaction_type_string
        });
      });
    }

    // --- ambil data user dari state global:auth
    const user = this.props.auth.user;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];

    //const { current_page, page_size, total } = this.props.accountHistory;
    const fileName = getLanguage({ id: "accountHistoryReports.pageHeader" });

    return (
      <LayoutWrapper>
        <Box>
          <PageHeader
            marginBottom={true}
            title={<IntlMessages id="accountHistories.pageHeader" />}
          >
            <PrintWrapper
              componentRef={ref => (this.componentRef = ref)}
              title={<IntlMessages id="accountHistories.pageHeader" />}
              columns={columns}
              data={dataSource}
              filter={filter_select}
              locations={locations}
            />

            <ReactToPrint
              trigger={() => <ButtonPrint loading={loading} />}
              content={() => this.componentRef}
              onBeforePrint={() =>
                (document.title = getFileName(fileName, null, false))
              }
              onAfterPrint={() => (document.title = siteConfig.documentTitle)}
              pageStyle={
                "@page { size: landscape; }" +
                "body {" +
                "-webkit-print-color-adjust: exact;" +
                "} "
              }
            />

            <PDFWrapper
              columns={columns}
              data={dataSource}
              fileName={fileName}
              filter={filter_select}
              margin=".7cm"
              landscape={true}
              loading={loading}
              locations={locations}
              scale={0.5}
              title="accountHistories.pageHeader"
            />

            <ExcelWrapper
              title="accountHistories.pageHeader"
              fileName={fileName}
              loading={loading}
              columns={columns}
              data={excelDataSource}
              filter={filter_select}
              locations={locations}
            />

            <Button
              style={{ marginLeft: "5px" }}
              onClick={() => this.props.history.replace(redirectTo)}
            >
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>
          <div className="isoInvoiceTableBtn">
            <Search
              placeholder={`${getLanguage({
                id: "accountCode"
              })}, ${getLanguage({ id: "transactionNo" })}`}
              onSearch={this.handleSearch}
              style={{ marginRight: "1%" }}
              value={this.state.search}
              onChange={this.handleSearchValue}
            />
            <div
              style={{
                width: "20%",
                marginTop: "auto",
                marginRight: "1%"
              }}
            >
              <Button
                type="primary"
                onClick={this.handleSearch.bind(this, this.state.search)}
              >
                <IntlMessages id="button.searchFilter" />
              </Button>
            </div>
            <div
              style={{
                width: "30%",
                marginTop: "auto",
                marginRight: "27%"
                // marginLeft: "-2%"
              }}
            >
              <Button onClick={this.handleResetSearch}>
                <IntlMessages id="filter.reset" />
              </Button>
            </div>
          </div>

          {/* Filter */}

          <Filter
            accounts={accounts}
            end_date={this.state.filter.end_date}
            handleFilter={this.handleFilter}
            handleFilterChange={this.handleFilterChange}
            handleResetFilter={this.handleResetFilter}
            handleSelectAccount={this.handleSelectAccount}
            id_account={this.state.filter.id_account}
            id_location={this.state.filter.id_location}
            loadingForeign={loadingForeign}
            locations={locations}
            sort_by={this.state.filter.sort_by}
            sort_type={this.state.filter.sort_type}
            start_date={this.state.filter.start_date}
          />

          {/* /Filter */}

          <Divider orientation="left" style={{ fontSize: 14 }}>
            <IntlMessages id="details" />
          </Divider>

          <CardWrapper>
            {!filter ? (
              <Empty description={<IntlMessages id="clicksearchFilter" />} />
            ) : loading ? (
              <Spin></Spin>
            ) : !loading && dataSource.length === 0 ? (
              <Empty
                description={<IntlMessages id="accountHistories.noData" />}
              />
            ) : (
              <div className="isoInvoiceTable">
                <Scrollbars style={{ width: "100%" }}>
                  <TableWrapper
                    bordered={true}
                    dataSource={dataSource}
                    columns={columns}
                    className="invoiceListTable forceHeight"
                    loading={loading}
                    pagination={false}
                  />
                </Scrollbars>
              </div>
            )}
          </CardWrapper>
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    accountHistoryReports: state.accountHistoryReport,
    auth: state.auth
  };
};

export default compose(connect(mapStateToProps, actions))(AccountHistoryReport);
